import apiService from "@/services/apiService.js";
import Info from "@/components/UI/Info.vue";
import TheFooter from "@/components/UI/TheFooter.vue";
import spiner from "@/components/UI/spiner.vue";
import shinyBtn from "@/components/UI/shinyBtn.vue";
import gsap from "gsap";

export default {
  data() {
    return {
      laptop: { src: require("assets/images/wall.png") },
      phone: { src: require("assets/images/phone.png") },
      timeline: ""
    };
  },
  components: {
    apiService,
    Info,
    TheFooter,
    spiner,
    shinyBtn
  },
  asyncData(context) {
    return apiService.getBootcamps().then(res => {
      const arr = res.data.data.slice(0, 4);
      return {
        loadedBootcamp: arr
      };
    });
  },
  methods: {
    click() {
      const timeline = this.timeline;
      timeline.reversed() ? timeline.play() : timeline.reverse();

      setTimeout(() => {
        this.$router.push("/bootcamps");
      }, 1500);
    }
  },
  computed: {
    loggedIn() {
      return this.$store.getters.loggedIn;
    },
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  mounted() {
    this.timeline = gsap.timeline({ defaults: { duration: 0.8 } });
    this.timeline
      .from(
        "#text",
        {
          opacity: 0,
          y: -50
        },
        "-=.2"
      )
      .from(
        "#btn",
        {
          opacity: 0,
          y: 50
        },
        "-=.4"
      )
      .from(
        "#phone",
        {
          opacity: 0,
          y: 50
        },
        "-=.1"
      )
      .from(
        "#laptop",
        {
          opacity: 0,
          x: 100
        },
        "-=1.5"
      );
  }
};
